<template>
  <div class="HomeLayout">
    <van-nav-bar fixed style="background-color: #F1F1F1">
      <div slot="left">
        <span>景洪 29℃</span>
        <br>
        <span>天气还不错~</span>
      </div>
      <div slot="title"
           class="nav-bar-title">
        象行网
      </div>
      <div slot="right">
        <van-button type="default"
                    class="nav-bar-button"
                    @click="goSearch">
          <v-icon name="search"></v-icon>
        </van-button>
        <van-button type="default"
                    class="nav-bar-button">
          <v-icon name="plus"></v-icon>
        </van-button>
      </div>
    </van-nav-bar>
    <div class="HomeLayout-helps">
      <div class="HomeLayout-helps-main">
        <v-icon name="plus"></v-icon>
        <div>发布</div>
      </div>
    </div>
    <div class="HomeLayout-main">
      <cube-tab-bar ref="tabNav"
                    v-model="selectedLabel"
                    :showSlider="true"
                    :useTransition="disabled"
                    :data="tabLabels" class="">
      </cube-tab-bar>
      <cube-slide ref="slide"
                  :loop="loop"
                  :initial-index="initialIndex"
                  :auto-play="autoPlay"
                  :show-dots="showDots"
                  :options="slideOptions"
                  @scroll="scroll"
                  @change="changePage">

        <!-- 热度榜 -->
        <cube-slide-item :style="{height: contentHeight}">
          <scroll ref="scroll"
                  :data="list"
                  :listenScrollEnd="true">
            <div class="scroll-main">
              <van-swipe :autoplay="3000"
                         :width="slideWidth"
                         :height="slideHeight"
                         class="scroll-main-slide">
                <van-swipe-item v-for="(img, index) in imgSlide1" :key="index">
                  <img class="scroll-main-slide-img" :src="img">
                </van-swipe-item>
              </van-swipe>
              <p v-for="i in 100 " :key="i">第一页 {{ i }}</p>
            </div>
          </scroll>
        </cube-slide-item>

        <!-- 推荐 -->
        <cube-slide-item :style="{height: contentHeight}">
          <scroll ref="scroll"
                  :data="list"
                  :listenScrollEnd="true">
            <div class="scroll-main">
              <van-swipe :autoplay="3000"
                         :width="slideWidth"
                         :height="slideHeight"
                         class="scroll-main-slide">
                <van-swipe-item v-for="(img, index) in imgSlide2" :key="index">
                  <img class="scroll-main-slide-img" :src="img">
                </van-swipe-item>
              </van-swipe>
              <div>
                <smallNews v-for="(smallNews, index) in newsDatas" :key="index" style="margin: 20px 0" :data="smallNews"></smallNews>
              </div>
              <p v-for="s in 50 " :key="s">第二页 {{ s }}</p>
            </div>
          </scroll>
        </cube-slide-item>

        <!-- 工作 -->
        <cube-slide-item :style="{height: contentHeight}">
          <scroll ref="scroll"
                  :data="list"
                  :listenScrollEnd="true">
            <div class="scroll-main">
              <van-swipe :autoplay="3000"
                         :width="slideWidth"
                         :height="slideHeight"
                         class="scroll-main-slide">
                <van-swipe-item v-for="(img, index) in imgSlide3" :key="index">
                  <img class="scroll-main-slide-img" :src="img">
                </van-swipe-item>
              </van-swipe>
              <p v-for="k in 100 " :key="k">第三页 {{ k }}</p>
            </div>
          </scroll>
        </cube-slide-item>

        <!-- 集市 -->
        <cube-slide-item :style="{height: contentHeight}">
          <scroll ref="scroll"
                  :data="list"
                  :listenScrollEnd="true">
            <div class="scroll-main">
              <van-swipe :autoplay="3000"
                         :width="slideWidth"
                         :height="slideHeight"
                         class="scroll-main-slide">
                <van-swipe-item v-for="(img, index) in imgSlide4" :key="index">
                  <img class="scroll-main-slide-img" :src="img">
                </van-swipe-item>
              </van-swipe>
              <p v-for="z in 100 " :key="z">第四页 {{ z }}</p>
            </div>
          </scroll>
        </cube-slide-item>

      </cube-slide>
    </div>
  </div>
</template>

<script>
  import scroll from '../components/xxw-scroll'
  import smallNews from '../components/xxw-smallNews'

  function findIndex(ary, fn) {
    if (ary.findIndex) {
      return ary.findIndex(fn)
    }
    /* istanbul ignore next */
    let index = -1
    /* istanbul ignore next */
    ary.some(function (item, i, ary) {
      const ret = fn.call(this, item, i, ary)
      if (ret) {
        index = i
        return ret
      }
    })
    /* istanbul ignore next */
    return index
  }

  export default {
    name: "HomeLayout",
    data() {
      return {
        newsDatas: [
          {
            label: '招聘',
            title: '前端开发工程师',
            text: '工作时间弹性,上下班自由,公司地段好,有大牛带入门,薪资待遇好,具体面谈,有机会组团旅游',
            fMedia: 'https://AkyHuang.github.io/xxw/jpgs/23.jpg',
            sMedia: 'https://AkyHuang.github.io/xxw/jpgs/53.jpg',
            tMedia: 'https://AkyHuang.github.io/xxw/jpgs/19.jpg',
            userAvatar: 'https://AkyHuang.github.io/xxw/avatar/1.png',
            userName: '完美的生活',
            time: '5天前',
          },
          {
            label: '求职',
            title: 'Python 后端工程师',
            text: '本人大三，熟悉Python/Go/Java等后端技术，求老板收留',
            fMedia: 'https://AkyHuang.github.io/xxw/jpgs/12.jpg',
            sMedia: 'https://AkyHuang.github.io/xxw/jpgs/44.jpg',
            tMedia: 'https://AkyHuang.github.io/xxw/jpgs/32.jpg',
            userAvatar: 'https://AkyHuang.github.io/xxw/avatar/7.png',
            userName: 'gendseo',
            time: '2天前',
          },
          {
            label: '出租',
            title: '1室1厅|学区房|楼下100米菜市场',
            text: '按季度收房租，3000/季度，家具齐全，精装修',
            fMedia: 'https://AkyHuang.github.io/xxw/jpgs/43.jpg',
            sMedia: 'https://AkyHuang.github.io/xxw/jpgs/33.jpg',
            tMedia: 'https://AkyHuang.github.io/xxw/jpgs/22.jpg',
            userAvatar: 'https://AkyHuang.github.io/xxw/avatar/3.png',
            userName: '爱生活的小王',
            time: '1天前',
          },
          {
            label: '出租',
            title: '1室1厅|学区房|楼下100米菜市场',
            text: '按季度收房租，3000/季度，家具齐全，精装修',
            fMedia: null,
            sMedia: 'https://AkyHuang.github.io/xxw/jpgs/33.jpg',
            tMedia: 'https://AkyHuang.github.io/xxw/jpgs/22.jpg',
            userAvatar: 'https://AkyHuang.github.io/xxw/avatar/3.png',
            userName: '爱生活的小王',
            time: '1天前',
          }
        ],
        imgSlide1: [
          require('../assets/jpgs/1.jpg'),
          require('../assets/jpgs/2.jpg'),
          require('../assets/jpgs/3.jpg')
        ],
        imgSlide2: [
          require('../assets/jpgs/4.jpg'),
          require('../assets/jpgs/5.jpg'),
          require('../assets/jpgs/6.jpg')
        ],
        imgSlide3: [
          require('../assets/jpgs/7.jpg'),
          require('../assets/jpgs/8.jpg'),
          require('../assets/jpgs/9.jpg')
        ],
        imgSlide4: [
          require('../assets/jpgs/23.jpg'),
          require('../assets/jpgs/53.jpg'),
          require('../assets/jpgs/19.jpg')
        ],
        list: [],
        contentHeight: '',
        contentWidth: '',
        slideWidth: document.documentElement.clientWidth - 20,
        slideHeight: 120,
        disabled: false,
        selectedLabel: '推荐',
        tabLabels: [
          {
            label: '热度榜'
          },
          {
            label: '推荐'
          },
          {
            label: '工作'
          },
          {
            label: '集市'
          },
        ],
        loop: false,
        autoPlay: false,
        showDots: false,
        slideOptions: {
          listenScroll: true,
          probeType: 3,
          /* lock y-direction when scrolling horizontally and  vertically at the same time */
          directionLockThreshold: 0
        },
        slideOptions1: {
          eventPassthrough: 'horizontal',
          stopPropagation: true,

        },
        scrollOptions: {
          /* lock x-direction when scrolling horizontally and  vertically at the same time */
          directionLockThreshold: 0
        },
      }
    },
    methods: {
      changePage(current) {
        this.selectedLabel = this.tabLabels[current].label
      },
      scroll(pos) {
        const x = Math.abs(pos.x)
        const tabItemWidth = this.$refs.tabNav.$el.clientWidth
        const slideScrollerWidth = this.$refs.slide.slide.scrollerWidth
        const deltaX = x / slideScrollerWidth * tabItemWidth
        this.$refs.tabNav.setSliderTransform(deltaX)
      },
      getWH() {
        this.$nextTick(() => {
          var height = document.documentElement.clientHeight
          this.contentHeight = height - 126 + 'px'
          var width = document.documentElement.clientWidth
          this.contentWidth = width + 'px'
        })
      },
      goSearch() {
        this.$router.push('/search/')
      }
    },
    computed: {
      initialIndex() {
        let index = 0
        index = findIndex(this.tabLabels, item => item.label === this.selectedLabel)
        return index
      }
    },
    components: {
      scroll,
      smallNews
    },
    mounted() {
      this.getWH()
    }
  }
</script>

<style lang="less">
  @activeColor: salmon;
  @BGColor: #F1F1F1;

  .HomeLayout-helps {
    // 定位
    position: fixed;
    bottom: 60px;
    right: 10px;
    z-index: 10;

    // 样式
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: @activeColor;

    &-main {
      // 定位
      position: absolute;
      top:50%;
      left:50%;
      -webkit-transform: translate(-50%,-50%);
      -moz-transform: translate(-50%,-50%);
      transform:translate(-50%,-50%);

      // 样式
      text-align: center;
      font-size: 12px;
      .fa-icon {
        width: auto;
        height: 1em;
        max-width: 100%;
        max-height: 100%;
      }
    }
  }

  .cube-tab_active {
    color: @activeColor;
  }

  .cube-tab-bar-slider {
    background-color: @activeColor;
  }

  .HomeLayout-main {
    margin-top: 50px;
  }

  .scroll-main {
    padding: 5px 10px;
    background-color: @BGColor;
    .scroll-main-slide {
      border-radius: 8px;
      .scroll-main-slide-img {
        border-radius: 8px;
        width: 100%;
        height: 120px;
      }
    }
  }

  .nav-bar-button {
    background: none;
    border: none;
  }

  .nav-bar-title {
    color: @activeColor;
  }

  .van-nav-bar__left {
    top: 8px;
    font-size: 12px;
    height: 16px;
    line-height: 16px;
  }
</style>