<template>
  <div class="smallNews">

    <van-row type="flex"
             class="smallNews-header">
      <van-col span="5"
               align="center"
               class="smallNews-header-label">
        <span>{{ data.label }}</span>
      </van-col>
      <van-col span="19" align="center">
        <span>{{ data.title }}</span>
      </van-col>
    </van-row>

    <van-row type="flex" class="smallNews-text">
      <van-col span="24">
        <span>{{ data.text }}</span>
      </van-col>
    </van-row>

    <van-row type="flex"
             gutter="2"
             class="smallNews-media"
             v-if="data.fMedia !== null">
      <van-col span="16">
        <img class="smallNews-media-main" :src="data.fMedia">
      </van-col>
      <van-col span="8">
        <img class="smallNews-media-subMain" :src="data.sMedia">
        <img class="smallNews-media-subMain" :src="data.tMedia">
      </van-col>
    </van-row>

    <van-row type="flex" class="smallNews-footer">
      <van-col span="3">
        <img class="smallNews-footer-avatar" :src="data.userAvatar">
      </van-col>
      <van-col span="8">
        <span>{{ data.userName }}</span>
      </van-col>
      <van-col span="3">
        <span>{{ data.time }}</span>
      </van-col>
    </van-row>

  </div>
</template>

<style lang="less">
  @mainColor: salmon;
  @border-radius: 5px;

  .smallNews {
    white-space: pre-wrap;
    word-break: break-all;
    border-bottom: 1px @mainColor solid;
    border-top: 1px @mainColor solid;
    .smallNews-header {
      line-height: 24px;
      height: 24px;
      .smallNews-header-label{
        background-color: @mainColor;
        border-radius: 0 0 @border-radius @border-radius;
      }
    }

    .smallNews-text {
      font-size: 14px;
      margin: 5px 0;
    }

    .smallNews-media {
      height: 100px;
      .smallNews-media-main {
        width: 100%;
        height: 100%;
      }
      .smallNews-media-subMain {
        width: 100%;
        height: 48%;
      }
    }

    .smallNews-footer {
      line-height: 24px;
      height: 24px;
      font-size: 12px;
      margin: 5px 0 5px 5px;
      .smallNews-footer-avatar {
        height: 24px;
        width: 24px;
        border-radius: 50%;
      }
    }
  }
</style>

<script>
  export default {
    props: {
      data: {
        type: Object,
        default: function () {
          return []
        }
      }
    }
  }
</script>